body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-container {
  margin-top: 120px;
}

.cart-container {
  margin-top: 180px;
}

.btn-outline-custom, .btn-custom {
  color: #A93133;
  border-color: #A93133;
}

.btn-outline-custom:hover, .btn-custom:hover {
  color: #212529;
  background-color: #A93133;
  border-color: #A93133;
}

.badge-custom {
  color: #fff;
  background-color: #A93133;
}

.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #A93133;
  outline: 0;
  box-shadow: unset;
}